@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@200;300;400;500;600;700;800&display=swap);
body {
  margin: 0;
  font-family: Dosis, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f1f1f1;
}
:root {
    --black: #4a4a4a;
    --orange: #eb4a36;
    --white: #f1f1f1;
}
.container {
    width: 100%;
}

.logo-box {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}


.logo {
    width: 120px;
    bottom: 27px;
    margin: 10px;
    margin-top: 20px;
}

.main {
    width: 98%;
    height: 350px;
    position: relative;
    left: 50%;
    top: -15%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
}

.main img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: top;
    position: absolute;
    border-radius: 10px;
}

.main-side {
    padding-top: 90px;
    padding-left: 60px;
    position: absolute;
}

.main-side h3 {
    color: #f1f1f1;
    font-size: 2rem;
    font-weight: 600;
}

.search-input {
    height: 35px;
    width: 300px;
    background-color: #f1f1f1;
    display: flex;
    justify-content: space-between;
    border-radius: 5px;
}

.search-input input {
    background-color: transparent;
    outline: none;
    border: none;
    margin-left: 10px;
    width: 70%;
}

.main-side button {
    background-color: #eb4a36;
    border: none;
    width: 70px;
    border-radius: 5px;
    cursor: pointer;
    height: 30px;
    position: relative;
    top: 50%;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
    margin-right: 2px;
}

.search-input button:hover {
    background-color: #ee6755;
}

@media only screen and (max-width: 600px) {
    .main-side {
        padding-top: 150px;
        padding-left: 40px;
    }
}
.quick-navbar {
    width: 98%;
    margin-top: -15px;
    border-bottom: 1px solid #d1d1d1;
    position: relative;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
}

nav {
    position: relative;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    width: 800px;
    height: 50px;
}

nav ul {
    display: flex;
    list-style: none;
}

nav ul li {
    flex-grow: 1;

}

nav ul li a {
    text-decoration: none;
    color: var(--black);
    font-weight: 700;
    font-size: 17px;
    border-bottom: 3px solid transparent;
    padding: 14px 50px;
    line-height: 50px;
}

nav ul li a:hover {
    color: var(--orange);
}

.active {
    color: var(--orange);
    border-bottom-color: var(--orange);
}

.quick-section {
    width: 98%;
    height: 370px;
    position: relative;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
}

@media only screen and (max-width: 600px) {
    nav {
        width: 100%;
    }

    nav ul li a {
        padding: 14px 10px;
    }

    .nav-icon {
        display: none;
    }

}
.recipe-box {
    height: 330px;
    width: 190px;
    border-radius: 10px;
    margin: 20px 5px;
    position: relative;
    flex-shrink: 0;
    cursor: pointer;
}

.recipe-img {
    width: 100%;
    height: 250px;
}

.recipe-img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 10px 10px 0 0;
    opacity: .8;
}

.recipe-img img:hover {
    opacity: 1;
}

.img-opacity {
    width: 100%;
    height: 20px;
    background-image: linear-gradient(transparent, var(--white));
    position: absolute;
    z-index: 10;
    bottom: 80px;
}

.recipe-info {
    width: 100%;
    height: auto;
    position: relative;
}

.recipe-name {
    margin: 0;
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 700;
    color: var(--black);
}

.recipe-categories {
    margin: 0;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 500;
    color: var(--orange);
}

.recipe-bottom-info {
    height: 20px;
    position: absolute;
    bottom: -20px;
}

.recipe-stars {
    margin: 0;
}

.recipe-stars span {
    font-weight: 600;
    font-size: 13px;
}

.recipe-time {
    padding-right: 10px;
    text-transform: uppercase;
    font-size: 12px;
    font-weight: 500;
}
.slider {
    width: 100%;
    position: relative;
    height: 100%;
    display: flex;
    overflow: hidden;
    scroll-behavior: smooth;
}

.nav-loading-gif {
    width: 50px;
    position: relative;
    top: 40%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
}

.button {
    position: absolute;
    top: 50%;
    -webkit-transform: translate(0, -50%);
            transform: translate(0, -50%);
    padding: 30px 15px;
    border: none;
    color: var(--black);
    cursor: pointer;
    transition: .1s ease-in-out;
    z-index: 10;
}

.button:hover {
    color: black;
    padding: 40px 20px;
}

.quick-section-left-button {
    left: -5px;
    border-radius: 0 45px 45px 0;
}

.quick-section-left-button:hover {
    border-radius: 0 60px 60px 0;
}

.quick-section-right-button {
    right: -5px;
    border-radius: 45px 0 0 45px;
}

.quick-section-right-button:hover {
    border-radius: 60px 0 0 60px;
}

.button-left-arrow {
    padding-right: 10px;
}

.button-right-arrow {
    padding-left: 10px;
}
.recipes-container {
    width: 70%;
    /* height: auto; */
    position: relative;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
}

.recipes-header {
    text-align: center;
}

.recipes-header h3 {
    padding-top: 40px;
    text-transform: uppercase;
    color: var(--black);
    font-size: 35px;
    font-weight: 800;
    letter-spacing: 3px;
}

.recipes-header p {
    text-transform: uppercase;
    color: var(--orange);
    position: relative;
    bottom: 20px;
    font-size: 12px;
    font-weight: 600;
    letter-spacing: 2px;
}

.recipes-flex {
    width: 100%;
    height: auto;
    margin-top: 70px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.loading-gif {
    width: 50px;
    margin: 50px;
}

.pagination {
    margin: 50px 0;
    width: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

@media only screen and (max-width: 600px) {
    .pagination {
        width: 130%;
    }
}
@-webkit-keyframes SidebarShow {
    from {
        right: -90%;
    }

    to {
        right: 0;
    }
}

@keyframes SidebarShow {
    from {
        right: -90%;
    }

    to {
        right: 0;
    }
}

@-webkit-keyframes SidebarHide {
    from {
        right: 0;
    }

    to {
        right: -90%;
    }
}

@keyframes SidebarHide {
    from {
        right: 0;
    }

    to {
        right: -90%;
    }
}

.sidebar-recipe {
    width: 90%;
    height: 90vh;
    right: -90%;
    top: 5%;
    position: fixed;
    background-color: var(--white);
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    z-index: 30;
    border-radius: 20px 0 0 20px;
    display: flex;
}

.sidebar-recipe-info {
    width: 65%;
    height: 100%;
}

.sidebar-recipe-img {
    width: 35%;
    height: 100%;
}

.sidebar-recipe-img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.sidebar-recipe-info h3 {
    text-align: center;
    font-size: 26px;
}

.sidebar-recipe-info p {
    margin: 0 40px;
    margin-top: 40px;
    letter-spacing: 1px;
    word-spacing: 2px;
    font-size: 16px;
}

.sidebar-recipe-info button {
    position: absolute;
    margin: 30px;
    border: none;
    background-color: var(--white);
    border-radius: 100%;
    padding: 12px 16px;
    box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
    cursor: pointer;
    opacity: .5;
    transition: .15s ease-in;
}

.sidebar-recipe-info button:hover {
    opacity: 1;
}

.sidebar-flex {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: space-between;
    margin-top: 5%;
}

.sidebar-recipe-info-ingredients {
    width: 20%;
    height: auto;
}

.sidebar-recipe-info-ingredients h4 {
    margin-left: 40px;
}

.sidebar-recipe-info-ingredients li {
    font-size: 12px;
    font-weight: 600;
    margin-top: 5px;
    color: var(--orange);
}

.sidebar-recipe-info-instructions {
    width: 70%;
    height: auto;
}

.sidebar-recipe-info-instructions ul {
    position: relative;
    right: 30px;
}

.sidebar-info-instruction-element {
    display: flex;
    align-items: flex-start;
    width: 100%;
    height: auto;
    margin-top: 10px;
}

.sidebar-info-instruction-element li {
    list-style: none;
    font-size: 12px;
    font-weight: 600;
}

.sidebar-info-instruction-element p {
    margin: 0;
    margin-right: 5px;
    font-weight: bold;
    color: var(--orange);
}

.sidebar-show-animation {
    -webkit-animation-name: SidebarShow;
            animation-name: SidebarShow;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}

.sidebar-hide-animation {
    -webkit-animation-name: SidebarHide;
            animation-name: SidebarHide;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
    -webkit-animation-duration: .5s;
            animation-duration: .5s;
    -webkit-animation-timing-function: ease-in-out;
            animation-timing-function: ease-in-out;
}

@media only screen and (max-width: 1400px) {
    .sidebar-recipe-info-ingredients li {
        margin-top: -1px;
        font-size: 10px;
    }

    .sidebar-recipe-info-ingredients h4 {
        margin-top: 0;
    }

    .sidebar-info-instruction-element li {
        margin-top: -8px;
        font-size: 11px;
    }

    .sidebar-info-instruction-element p {
        margin-top: -8px;
        font-size: 12px;

    }
}

@media only screen and (max-width: 600px) {
    .sidebar-recipe-img {
        display: none;
    }

    .sidebar-recipe-info {
        width: 100%;
    }

    .sidebar-flex {
        flex-direction: column;
    }

    .sidebar-recipe-info-ingredients {
        width: 100%;
    }

    .sidebar-recipe-info-ingredients h4 {
        display: none;
    }

    .sidebar-recipe-info-ingredients li {
        margin-top: -1px;
        font-size: 8px;
    }

    .sidebar-recipe-info-ingredients ul {
        margin-top: 0;
    }

    .sidebar-recipe-info-instructions {
        width: 100%;
    }

    .sidebar-recipe-info-instructions ul {
        margin-top: 0;
    }

    .sidebar-info-instruction-element li {
        margin-top: -9px;
        font-size: 8px;
    }

    .sidebar-info-instruction-element p {
        margin-top: -9px;
        font-size: 9px;
    }
}
